import React, { useEffect, useState } from "react";


const App = () => {

    const [city, SetCity] = useState(null);
    const [search, SetSearch] = useState("Yavatmal");

    const InputEvent = (e) => {
        SetSearch(e.target.value)
    }

    useEffect(() => {
        const temps = async () => {
            let response = await fetch(`http://api.openweathermap.org/data/2.5/weather?q=${search}&units=metric&appid=6a0139c9046fbdf7a29165bbbfbc373a`)
            response = await response.json()
            console.log(response)
            SetCity(response.main)
        }
        temps()
    }, [search])
    return (
        <>
            <div className="container d-flex justify-content-center my-5" >
                <div className="card my-5 " style={{backgroundColor: "skyblue" }}>
                    <div className="card-body mx-5">
                        <h1 className="card-title text-center ">Weather App</h1>
                        <input type="search" class="form-control" id="exampleFormControlInput1"
                            placeholder="Enter Your City" onChange={InputEvent} />
                        {!city ? (
                            <p className="text-center my-4" style={{ color: "red" }}>Not Data Found </p>
                        ) : <>
                            <h2 className=" my-4 text-capitalize"><i className="fas fa-street-view mx-4"></i>{search}</h2>
                            <h1 className="text-center">{city.temp}°Cel</h1>
                            <p className="text-center fw-light">Min : {city.temp_min}°Cel |  Max : {city.temp_max}°Cel</p>
                       
                        </>
                        }
                    </div>
                </div>
            </div>
        </>
    )
}
export default App;

